/* ThankYouPage.css */
.thank-you-container {
    text-align: center;
    max-width: 600px;
    margin: 3rem auto;
    padding: 2rem;
    background: #ffffffcc;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  }
  
  .thank-you-container h1 {
    color: #4CAF50; /* Positive, welcoming color */
    margin-bottom: 1rem;
  }
  
  .thank-you-container p {
    margin-bottom: 1rem;
    line-height: 1.6;
    color: #555; /* Soft text color for readability */
  }
  
  .thank-you-container form {
    margin-top: 2rem;
  }
  
  .thank-you-container input[type='email'] {
    padding: 1rem;
    margin-bottom: 1rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    width: calc(100% - 2rem); /* Full width minus padding */
  }
  
  .thank-you-container .submit-button {
    background-color: #3498db;
    color: white;
    padding: 1rem 2rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
    width: 100%;
  }
  
  .thank-you-container .submit-button:hover {
    background-color: #2980b9; /* Darker blue on hover */
  }
  