main {
    background-color: rgba(255, 255, 255, 0.9); /* 투명도를 준 배경 */
    color: #333; /* 어두운 글자색을 조정합니다 */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* 텍스트 그림자 추가 */
    margin: 2rem;
    padding: 2rem;
    border-radius: 15px; /* 모서리 둥글게 */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 600px;
  }