/* Header.css */
header {
    position: fixed; /* 고정 위치 */
    top: 0; /* 상단에 배치 */
    left: 0; /* 좌측에 배치 */
    right: 0; /* 우측에 배치 */
    background-color: rgba(0, 0, 0, 0.7); /* 투명도를 준 배경 */
    color: white;
    padding: 1rem 2rem;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
    z-index: 1000; /* 다른 요소들 위에 표시되도록 z-index 설정 */
  }
  
  h1 {
    font-size: 2rem;
    margin: 0; /* 추가된 부분: 상하 마진 제거 */
  }
