/* DateInput.css */
form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: rgba(255, 255, 255, 0.9);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    margin: 2rem auto;
  }
  
  form label {
    font-size: 1rem;
    color: #333;
  }
  
  form input {
    font-size: 1rem;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button {
    background-color: #4A5568; /* 진한 회색 */
    color: white;
    padding: 1rem;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #2D3748; /* 호버 시 배경색 변경 */
  }
  
  /* DateInput.css 추가 */
.input-group {
    display: flex;
    flex-direction: column;
  }
  
  .input-group label {
    margin-bottom: .5rem;
  }
  
  /* 체크박스 스타일링 */
  .checkbox-group {
    display: flex;
    align-items: center;
  }
  
  .checkbox-group input[type="checkbox"] {
    margin-right: .5rem;
  }
  
/* DateInput.css에 추가 */
.loading-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-size: 1.5rem;
    text-align: center;
    font-family: 'Arial', sans-serif; /* 폰트 패밀리 변경 */
    color: #3498db; /* 글자 색상 변경 */
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  .spinner {
    border: 8px solid rgba(0, 0, 0, 0.1); /* Light grey */
    border-top: 8px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 2s linear infinite;
  }
  
  .loading-message {
    margin-top: 20px;
    font-size: 20px; /* 폰트 사이즈 증가 */
    font-weight: bold; /* 굵은 글씨 */
    color: #ffffff; /* 글씨 색상 변경 */
  }