/* ResultsPage.css */
.results-container {
    text-align: center;
  }
  
  .birthdate-info {
    margin-bottom: 2rem;
  }
  
  .saju-image-container img {
    max-width: 50%;
    height: auto;
  }
  
  .general-fortune {
    font-size: 1.25rem; /* 글씨 크기 조절 */
    margin: 0; /* 위 아래 마진 조절 */
  }
  
  .cta-container {
    position: fixed; /* 버튼을 화면에 고정 */
    left: 0; /* 왼쪽 가장자리와 정렬 */
    right: 0; /* 오른쪽 가장자리와 정렬 */
    bottom: 0; /* 화면 하단에 위치 */
    padding: 1rem; /* 버튼과 화면 가장자리 사이의 여백 */
    text-align: center; /* 버튼 가운데 정렬 */
    box-shadow: 0 -2px 5px rgba(0,0,0,0.1); /* 상단에 그림자 효과 */
  }
  
  .cta-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #3498db;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 0 auto; /* 자동 마진으로 버튼을 중앙에 위치 */
  }
  
  .blurred-text {
    color: #999;
    margin: 0;
    filter: blur(3px); /* 텍스트 블러 적용 */
    overflow: hidden;
    height: 30vh;
  }