.App {
  background-image: url('./main.webp'); /* Replace with the actual image URL */
  background-size: cover;
  background-position: center center; /* 배경 이미지를 가운데로 정렬 */
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: #ffffff; /* 텍스트 색상 */
  font-family: 'Noto Sans KR', sans-serif; /* 한글에 최적화된 서체 사용 */
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-content {
  padding-top: 68px; /* 예시로 넣은 숫자입니다. 헤더의 실제 높이에 맞게 조절해야 할 수도 있습니다. */
}
